"use client";

import React, { useState, useRef, useEffect, forwardRef, CSSProperties } from "react";

// --- START: Missing Dependencies ---
// To make this component self-contained, we are defining the missing
// types, components, and hooks directly in this file.

// 1. Missing Types from "../lib/types"
const DEFAULT_THEME = {
  // Define some default theme properties if needed
  messageBackgroundColor: '#f0f0f0',
  userMessageBackgroundColor: '#dcf8c6',
};

interface User {
  id: string;
  name?: string;
  avatar?: string;
}

interface Message {
  id: string;
  content: string;
  sender: 'You' | 'Other' | 'System';
  timestamp: string;
  reactions?: { [key: string]: string[] };
}

// 2. Mock for "@/components/ui/button" and "@/components/ui/input"
const Button = forwardRef<HTMLButtonElement, React.ButtonHTMLAttributes<HTMLButtonElement> & { variant?: string; size?: string }>(
  ({ className, variant, size, ...props }, ref) => {
    return (
      <button
        className={`${className} inline-flex items-center justify-center rounded-md text-sm font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50`}
        ref={ref}
        {...props}
      />
    );
  }
);
Button.displayName = "Button";

const Input = forwardRef<HTMLInputElement, React.InputHTMLAttributes<HTMLInputElement>>(
  ({ className, ...props }, ref) => {
    return (
      <input
        className={`${className} flex h-10 w-full rounded-md border border-input bg-background px-3 py-2 text-sm ring-offset-background file:border-0 file:bg-transparent file:text-sm file:font-medium placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50`}
        ref={ref}
        {...props}
      />
    );
  }
);
Input.displayName = "Input";

// 3. Mock for "./chat/chat-message-item"
const ChatMessageItem: React.FC<{
  message: Message;
  user: User;
  onOpenEmojiMenu: () => void;
}> = ({ message, onOpenEmojiMenu }) => {
  const isYou = message.sender === "You";
  return (
    <div className={`flex ${isYou ? "justify-end" : "justify-start"} mb-2`}>
      <div
        data-message-id={message.id}
        className={`max-w-xs rounded-lg p-3 ${
          isYou
            ? "bg-green-200 text-gray-800"
            : "bg-gray-200 text-gray-800"
        }`}
        onClick={onOpenEmojiMenu} // Allow opening emoji menu on click
      >
        <p>{message.content}</p>
      </div>
    </div>
  );
};


// 4. Mock for "../hooks/use-actual-messages"
const useActualMessages = (messages: Message[]) => {
  // In a real app, this might filter out pending or deleted messages.
  // For this self-contained component, we'll just return the messages as-is.
  return messages;
};

// 5. Mock for "./chat/emoji-overlay"
const EmojiOverlay: React.FC<{ open: boolean; onClose: () => void; }> = ({ open, onClose }) => {
  if (!open) return null;
  // A simple placeholder for the emoji overlay
  return (
    <div 
      className="fixed inset-0 bg-black/30 z-50 flex items-center justify-center"
      onClick={onClose}
    >
      <div 
        className="bg-white p-4 rounded-lg shadow-xl"
        onClick={(e) => e.stopPropagation()} // Prevent closing when clicking inside
      >
        <p className="text-center font-semibold">Emoji Reactions</p>
        <div className="flex justify-center space-x-2 mt-2">
          <span>üëç</span>
          <span>‚ù§Ô∏è</span>
          <span>üòÇ</span>
          <span>üòÆ</span>
          <span>üò¢</span>
        </div>
         <button onClick={onClose} className="mt-4 w-full text-center text-sm text-blue-500">Close</button>
      </div>
    </div>
  );
};

// --- END: Missing Dependencies ---

interface ChatProps {
  messages: Message[];
  sendMessage: (text: string) => void;
  onReact: (messageId: string, reaction?: string | null) => Promise<void>;
  endChat: () => void;
  peerId: string;
  status: string;
}

export default function Chat({
  messages,
  sendMessage,
  endChat,
  onReact,
  peerId,
  status,
}: ChatProps) {
  const user: User = { id: peerId };

  const [currentMessage, setCurrentMessage] = useState("");
  const chatLogRef = useRef<HTMLDivElement>(null);

  const [emojiMenuState, setEmojiMenuState] = useState<{
    open: boolean;
    message?: Message | null;
  }>({ open: false });

  const actualMessages = useActualMessages(messages);

  const onOpenEmojiMenu = (message: Message | null) => {
    if (!message) {
      setEmojiMenuState({ open: false });
      return;
    }
    setEmojiMenuState({ open: true, message });
  };

  // Auto-scroll to the latest message
  useEffect(() => {
    if (chatLogRef.current) {
      chatLogRef.current.scrollTop = chatLogRef.current.scrollHeight;
    }
  }, [messages]);

  const handleSend = (e: React.FormEvent) => {
    e.preventDefault();
    const trimmedMessage = currentMessage.trim();
    if (trimmedMessage) {
      sendMessage(trimmedMessage);
      setCurrentMessage("");
    }
  };

  const renderMessage = (msg: Message, index: number) => {
    if (msg.sender === "System") {
      return (
        <div key={index} className="text-center my-2">
          <span className="text-xs text-gray-500 bg-gray-100 rounded-full px-3 py-1">
            {msg.content}
          </span>
        </div>
      );
    }

    return (
      <ChatMessageItem
        key={msg.id || index}
        message={msg}
        user={user}
        onOpenEmojiMenu={() => onOpenEmojiMenu(msg)}
      />
    );
  };

  return (
    <>
      <EmojiOverlay
        open={emojiMenuState.open}
        onClose={() => onOpenEmojiMenu(null)}
        // The rest of the props are handled by the mock component
      />

      {/* Main chat container: h-screen makes it full height, sm:h-auto and sm:max-h-[90vh] for desktop */}
      <div
        id="chat-container"
        className="w-full max-w-md mx-auto h-screen sm:h-auto sm:max-h-[90vh] flex flex-col bg-white/70 backdrop-blur-xl sm:rounded-[2rem] shadow-2xl overflow-hidden"
      >
        {/* Chat Header */}
        <div className="p-4 bg-white/60 border-b border-gray-200/80 flex justify-between items-center shrink-0">
          <div className="text-left">
            <p className="text-xs font-medium text-gray-500">Status</p>
            <p className="text-sm font-semibold text-green-600">{status}</p>
          </div>
          <Button
            onClick={endChat}
            variant="destructive"
            size="sm"
            className="rounded-full bg-red-500 text-white hover:bg-red-600 px-4 py-1"
          >
            End Chat
          </Button>
        </div>

        {/* Disclaimer message below header */}
        <div className="p-2 text-center text-xs text-gray-500 bg-white/60 border-b border-gray-200/80 shrink-0">
          not all features are implemented, still in early stages -@tyronscott_
        </div>

        {/* Chat Log */}
        <div ref={chatLogRef} className="flex-grow overflow-y-auto p-4">
          {actualMessages.map(renderMessage)}
        </div>

        {/* Message Input Form */}
        <div className="p-4 bg-white/60 border-t border-gray-200/80 shrink-0">
          <form onSubmit={handleSend} className="flex items-center space-x-3">
            <Input
              type="text"
              value={currentMessage}
              onChange={(e) => setCurrentMessage(e.target.value)}
              placeholder="Type a message..."
              className="flex-grow p-3 border-gray-300 rounded-full focus:outline-none focus:ring-2 focus:ring-green-500 bg-white/80"
              autoComplete="off"
              required
            />
            <Button
              type="submit"
              className="bg-green-500 text-white font-bold w-12 h-12 rounded-full hover:bg-green-600 flex items-center justify-center shadow-lg"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="24"
                height="24"
                viewBox="0 0 24 24"
                fill="none"
                stroke="currentColor"
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
              >
                <line x1="12" y1="19" x2="12" y2="5"></line>
                <polyline points="5 12 12 5 19 12"></polyline>
              </svg>
            </Button>
          </form>
        </div>
      </div>
    </>
  );
}
